// ProjectCard.js

import React, { useState } from 'react';
import { motion } from 'framer-motion';
import './ProjectCard.css'; 
import { Bounce } from "react-swift-reveal";

const ProjectCard = ({ project }) => {
  const [flip, setFlip] = useState(false)
  const logging = () => {
    console.log(useState)
  }
  const renderImage = () => {
    if (project.img) {
      // If the project has an 'img' attribute, render the background image
      const backgroundImageStyle = {
        backgroundImage: `url(${require(`${project.img}`)})`,
      };

      return (
        <div
          className="image-container"
          style={backgroundImageStyle}
         
        />
      );
    }
  };


  console.log(project.img)
  return (
    <Bounce top count={1} distance="10px" delay={4} >
    <motion.div
      className={`project-card ${flip ? "flip" : ""}`}

      onClick={() => setFlip(!flip)}
      
    >
      
      <div className='front' onClick={() => setFlip(!flip)}>
      {renderImage()}
      <h3>{project.title}</h3>
      <span style={{ opacity:'50%' }}>click to flip</span>
      </div>
      <div className='back' onClick={() =>setFlip(!flip)}>
      <p>{project.description}</p>
      <a href={project.link} target="_blank" rel="noopener noreferrer">
        <button className='GHlink'>GitHub</button>
      </a>
      </div>
      
    </motion.div>
    </Bounce>
  );
};

export default ProjectCard;
