// Skills.js

import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHtml5, faCss3, faJs, faReact, faPython, faNodeJs  } from '@fortawesome/free-brands-svg-icons';
import './Skills.css'; // Don't forget to create this CSS file for styling
import ProgressBar from './ProgressBar';
import { motion, AnimatePresence } from 'framer-motion';

const skillsData = [
    {
        name: 'HTML',
        icon: <FontAwesomeIcon icon={faHtml5} />,
        proficiency: 95,
      },
      {
        name: 'CSS',
        icon: <FontAwesomeIcon icon={faCss3} />,
        proficiency: 75,
      },
      {
        name: 'JavaScript',
        icon: <FontAwesomeIcon icon={faJs} />,
        proficiency: 60,
      },
      {
        name: 'React',
        icon: <FontAwesomeIcon icon={faReact} />,
        proficiency: 45,
      },
      {
        name: 'Python',
        icon: <FontAwesomeIcon icon={faPython} />,
        proficiency: 75,
      },
      {
        name: 'NodeJS',
        icon: <FontAwesomeIcon icon={faNodeJs} />,
        proficiency: 65,
      },
      
      
];
const iconVariants = {
  hidden: { opacity: 0, y: -20 },
  visible: { opacity: 1, y: 0, transition: { duration: 0.5 } },
};

const Skills = () => {
  const [activeTab, setActiveTab] = useState('Icons');

  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };

  const renderContent = () => {
    if (activeTab === 'Icons') {
      return (
         <motion.div
          className="skills-container"
          initial="hidden"
          animate="visible"
          variants={{ visible: { transition: { staggerChildren: 0.1 } } }}
        >
          <AnimatePresence>
            {skillsData.map((skill, index) => (
              <motion.div
                key={index}
                className="skill-item"
                variants={iconVariants}
                exit={{ opacity: 0, y: 20, transition: { duration: 0.2 } }}
              >
                <p className='skill-icon'>{skill.icon}</p>
                <p className="skill-name">{skill.name}</p>
                <ProgressBar proficiency={skill.proficiency} />
              </motion.div>
            ))}
          </AnimatePresence>
          
        </motion.div>
      );
    } else if (activeTab === 'Details') {
      return (
        <div className='details-container'>
          <p></p>
          {/* Add more details as needed */}
        </div>
      );
    } else if (activeTab === 'AnotherTab') {
      // Add more tabs as needed
      return (
        <div className='another-tab-container'>
          <p>Content for Another Tab</p>
        </div>
      );
    }
  };

  return (
    <section id="skills">
      <div className="button-container2">
        <button
          className={`tab-button ${activeTab === 'Icons' ? 'active' : ''}`}
          onClick={() => handleTabChange('Icons')}
        >
          Icons
        </button>
        <button
          className={`tab-button ${activeTab === 'Details' ? 'active' : ''}`}
          onClick={() => handleTabChange('Details')}
        >
          Details
        </button>
        {/* Add more tab buttons as needed */}
      </div>
      {renderContent()}
    </section>
  );
};

export default Skills;